.m-share-mask {
    margin: 0;
    padding: 0;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 2147483646;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0;
    -webkit-transition: opacity .2s ease-in;
    transition: opacity .2s ease-in;
}
#m-share-actionSheet {
    margin: 0;
    padding: 0;
    font-family: "PingFang SC",Arial,"\5FAE\8F6F\96C5\9ED1",Helvetica,sans-serif;
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
    z-index: 2147483647;
    background: #fff;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 30px 20px;
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    -webkit-transition: -webkit-transform .2s ease-in;
    transition: transform .2s ease-in;
}

#m-share-actionSheet div {
    margin: 0;
    padding: 0;
}
#m-share-actionSheet .m-share-sheet-title {
    font-size: 12px;
    color: #ababab;
    text-align: center;
    margin:10px 0 0 0;
}
#m-share-actionSheet .m-share-flex {
    display: flex!important;
    flex-wrap: wrap
}
#m-share-actionSheet .m-share-flex>.m-share-cell {
    flex: 1;
    width: 0;
    flex-basis: 0;
    max-width: 100%;
    display: block;
    padding: 0!important;
    position: relative;
    text-align: center;
}
#m-share-actionSheet .m-share-iconfont {
    margin: 0;
}
.m-share-tips {
    font-family: "PingFang SC",Arial,"\5FAE\8F6F\96C5\9ED1",Helvetica,sans-serif;
    margin: 0;
    padding: 0;
    position: fixed;
    top: 3px;
    color: #fff;
    right: 20px;
    z-index: 2147483647;
}
.m-share-tips .m-share-tips-w {
    margin: 0;
    padding: 0;
    display: inline-block;
    font-size: 18px;
    position: relative;
    right: 1px;
    top: 7px;
}
.m-share-tips .m-share-tips-w .m-share-tips-p {
    margin: 0;
    padding: 0;
    color: #fff;
    font-size: 18px;
}
.m-share-tips-arrow {
    margin: 0;
    padding: 0;
    display: inline-block;
    width: 54px;
    height: 55px;
    background-image: url('data:image/png;base64,');
    background-size: 100%;
}
.m-share-tips-bottom {
    font-family: "PingFang SC",Arial,"\5FAE\8F6F\96C5\9ED1",Helvetica,sans-serif;
    margin: 0;
    padding: 0;
    font-size: 18px;
    position: fixed;
    color: #fff;
    top: 50%;
    left: 50%;
    width: 100%;
    z-index: 2147483647;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
    width: 240px;
    line-height: 1.72;
}

@font-face {font-family: "m-share-iconfont";
src: url('data:application/x-font-ttf;charset=utf-8;base64,') format('truetype'); /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
}
.m-share-iconfont {
    font-family:"m-share-iconfont" !important;
    font-size:26px;
    font-style:normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding: 5px;
    margin:0 0 0 10px;
}
.m-share-iconfont:first-child {
    margin: 0;
}
.m-share-iconfont-wx {
    color: #7bc549;
    border: 1px solid #7bc549;
    border-radius: 100%;
}
.m-share-iconfont-wx:active {
    background: #7bc549;
    color: #fff;
}
.m-share-iconfont-wx:before {
    content: "\e61e";
}

.m-share-iconfont-wxline {
    color: #33b045;
    border: 1px solid #33b045;
    border-radius: 100%;
}
.m-share-iconfont-wxline:active {
    background: #33b045;
    color: #fff;
}
.m-share-iconfont-wxline:before {
    content: "\e6e9";
}
.m-share-iconfont-menu {
    font-size: 12px;
}
.m-share-iconfont-menu:before {
    content: "\e67e";
}
.m-share-iconfont-dots {
    font-size: 25px;
    color: #fff;
}
.m-share-iconfont-dots:before {
    content: "\e656";
}
.m-share-iconfont-share {
    font-size: 17px;
    color: #fff;
    margin: 0;
}
.m-share-iconfont-share:before {
    content: "\e6ae";
}



.web-share {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", arial,
    sans-serif;
  font-weight: 400;
  font-size: 13px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

.web-share-title {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.12em;
  opacity: 0.7;
}

.web-share {
  position: fixed;
  top: -10px;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0);
  transition: background 0.4s;
  padding: 8px;
  flex-direction: column;
  justify-content: flex-end;
  z-index: 100000;
  display: none;
}

.web-share-container {
  max-width: 490px;
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto 8px;
  background: #f8f8f8;
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.5) 0 2px 4px;
  padding: 16px 23px;
  text-align: left;
  color: black;
  transform: translateY(10vh);
  opacity: 0;
  transition: transform 0.4s, opacity 0.4s;
}

.web-share-container.web-share-cancel {
  color: #0076ff;
  font-size: 16px;
  text-align: center;
  cursor: pointer;
}

.web-share-item {
  margin: 16px 8px;
  display: inline-block;
  text-decoration: none !important;
  color: black !important;
  cursor: pointer;
  width: 64px;
}

.web-share-item [class^="web-share-icon-"] {
  background-color: white;
  border-radius: 12px;
  padding: 12px;
  color: black;
  font-size: 32px;
  width: 32px;
  height: 32px;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 auto;
}

.web-share-item[href=""] {
  display: none;
}

.web-share-item-desc {
  text-align: center;
  margin-top: 4px;
}

.web-share.desktop .web-share-sms {
  display: none;
}

